import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
// http://front-ender.me/react-drag-list
import ReactDragList from 'react-drag-list';
import 'react-drag-list/assets/index.css';
import { fromJS } from 'immutable';
import SpotCard from './SpotCard';

const StyledContent = styled.div`
  padding: 15px 30px;
  .content__title {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  .content__title-day {
    font-size: 16px
  }
  .content__title-date {
    font-size: 12px;
  }
  .content__simple-drag {
    background: yellow;
  }
  .content__simple-drag-row {
    background: red;
  }
`;

const mockSpotData = fromJS([
  {
    id: 0,
    name: '101',
    address: '1107',
    startTime: '09:00',
  },
  {
    id: 1,
    name: '',
    address: '386',
    startTime: '10:00',
  },
  {
    id: 2,
    name: '()',
    address: '55',
    startTime: '12:00',
  },
  {
    id: 3,
    name: '',
    address: '598',
    startTime: '15:00',
  },
]);

const Content = (props) => {
  const {
    location,
  } = props;
  const searchParams = new URLSearchParams(location.search);
  const day = searchParams.get('day') || 1;
  return (
    <StyledContent>
      <div className="content__title">
        <div className="content__title-day">{`${day}`}</div>
        <div className="content__title-date">{`20196${parseInt(day, 10) + 4}`}</div>
      </div>
      <ReactDragList
        dataSource={mockSpotData}
        row={(spot) => (
          <SpotCard
            key={spot.get('id')}
            spot={spot}
          />
        )}
        handles={false}
        className="simple-drag"
        rowClassName="simple-drag-row"
        onUpdate={() => {}}
      />
    </StyledContent>
  );
};

Content.propTypes = {
  location: PropTypes.object,
};

Content.defaultProps = {
  location: {},
};

export default Content;
